import React from 'react';
import logo from './logo.svg';
import './App.scss';
import Home from "./components/Home";
import Resume from "./components/Resume";
import Skills from "./components/skills";
import Blog from "./components/Blog";
import Promotion from "./components/Promotion";
import Layout from "./components/layout";
import {BrowserRouter, Redirect,Router,Route, Switch} from 'react-router-dom'
import {Helmet} from "react-helmet";
import Footer from "./components/Footer";
import Navbar from "./components/Navbar";

class App extends React.Component {
  render(){
  return (
    <div className="App">
        <Helmet>
            <title>Asish Tom | Senior S E</title>
            <meta charSet="utf-8" />
            <link rel="blog" href="https://blog.asishtom.in" />
            <link rel="portfolio" href="https://portfolio.asishtom.in" />
            <meta name="description" content="Mr. Asish Tom is a Senior SE.
            He is focused on Python and Golang based micro services.
            He got total 4 years of experience " />
            <Home/>
        </Helmet>
        <Navbar/>
        <Layout key={"layout"}>
            <div className="layout">
                <Switch>
                    <Route exact path="/" >
                        <Redirect to="/home" />
                    </Route>
                    <Route path="/home" component={Home}/>
                    <Route path="/about" component={Resume}/>
                    <Route path="/skills" component={Skills}/>
                    <Route path="/blog" component={Blog}/>
                    <Route path="/promo" component={Promotion}/>
                </Switch>
                <Footer />

            </div>
        </Layout>
    </div>
  );
  }
}

export default App;
