import React from "react"
import scrollTo from "react-scroll"
import {
  BrowserRouter as Router,
  Switch,
  Route,
  NavLink,
} from "react-router-dom";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faBars, faCoffee} from "@fortawesome/free-solid-svg-icons";
class Navbar extends React.Component {
    toggleHam(){
        this.setState({isToggle: !this.state.isToggle});
    }
    constructor(props){
        super(props);
        this.state = {
          isToggle: true,
        };
         this.toggleHam = this.toggleHam.bind(this);
    }
    render() {
        let menuClassName = "links-wrapper show-from-mob"
        if (this.state.isToggle){
            menuClassName = "links-wrapper";
        } else{
            menuClassName="links-wrapper show-from-mob";
        }
        return (

            <div className="section">
                <div className="nav-container">
                    <div className="navbar-wrapper">
                        <FontAwesomeIcon  onClick={this.toggleHam} className="ham-menu" icon={faBars} />
                        <div
                            className="name"
                            role="button"
                            tabIndex={-1}
                            onKeyDown={()=>{}}
                        >
                            ASISH
                        </div>
                        <div  className={menuClassName}>
                            <NavLink className="link-selector"  activeClassName="active" to={"/home"} onClick={this.toggleHam}>HOME</NavLink>
                            <NavLink className="link-selector"   activeClassName="active" to={"/about"} onClick={this.toggleHam}>RESUME</NavLink>
                            <NavLink className="link-selector"   activeClassName="active" to={"/blog"} onClick={this.toggleHam}>BLOG</NavLink>
                            &nbsp;&nbsp;
                        </div >
                    </div>
                </div>
            </div>
        )
    }
}



export default Navbar
